.header {
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  /* flex-wrap: wrap; */
  justify-content: space-between;
  align-items: center;
  padding: 10px;
  background-color: var(--dark-gray-color);
  font-size: var(--large-font-size);
  color: var(--main-font-color);
  z-index: 2;
  /* border-bottom: 3px solid var(--active-color); */
  box-shadow: var(--box-shadow-color);
}

.modal {
  /* display: none; */
}

.menu--overlay {
  position: fixed;
  top: 0;
  right: 0;
  width: 100vw;
  height: 100vh;
  background-color: var(--overlay-color);
  opacity: 0.8;
  z-index: 1;
  animation: fadeIn;
  animation-duration: .5s;
}

.drop__menu {
  /* display: flex;
  flex-direction: column;
  justify-content: space-between; */
  width: auto;
  height: 100vh;
  position: fixed;
  top: 0;
  right: 0;
  /* border-left: 1px solid var(--active-shadow-color);
  border-bottom: 1px solid var(--active-shadow-color);
  border-bottom-left-radius: 10px; */
  z-index: 2;
  padding: 15px;
  background-color: var(--background-menu-color);
  box-shadow: var(--dark-shadow-color);
  font-size: var(--large-font-size);
  animation: fadeInRight;
  animation-duration: .7s;
}

.drop__menu .input--link {
  width: 100%;
  margin-top: 60px;
  margin-bottom: 30px;
}

.header .close--icon {
  top: 10px;
  right: 10px;
  z-index: 1;
}

.search__bar {
  display: flex;
  height: 40px;
  align-items: center;
  margin: 50px 0;
  background-color: var(--border-color);
  border-top-right-radius: 10px;
  border-bottom-right-radius: 10px;
  /* box-shadow: var(--box-shadow-color); */
}

.search--input {
  width: 80%;
  height: 100%;
  text-align: center;
  /* padding: 20px 0; */
  outline-style: none;
  color: var(--second-font-color);
  font-size: var(--large-font-size);
}

.button--submit {
  width: 100px;
  height: 100%;
  background-color: var(--second-font-color);
  outline-style: none;
  border: none;
}

.button--submit .search--icon {
  /* width: 20%; */
  /* height: 100%; */
  color: var(--main-font-color);
}

.button--submit .search--icon:hover {
  color: var(--second-font-color);
  /* text-shadow: var(--box-shadow-color); */
}

/* .drop__menu ~ {
  margin-bottom: 20px;
} */



.menu__list {
  display: block;
  color: var(--second-font-color);
  /* border: 3px solid var(--border-color) ; */
  /* box-shadow: var(--box-shadow-color); */
  /* padding: 5px;
  padding: 20px; */
}
.menu__list a {
  width: auto;
  height: auto;
  text-decoration: none;
  font-size: var(--jumbo-font-size);
  color: var(--second-font-color);
}
.list--item {
  display: flex;
  justify-content: flex-end;
  /* margin-bottom: 10px; */
  cursor: pointer;
  margin-right: 15px;
  /* position: relative; */
  /* position: relative; */
}
/* .list--item a {
  position: relative;
} */

.sub--menu {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  justify-content: center;
  align-items: center;
  gap: 10px;
  width: 500px;
  position: absolute;
  top: 100%;
  left: 10%;
  padding: 10px;
  /* right: 50%; */
  /* left: 50%; */
  background-color: var(--dark-gray-color);
  /* border: 1px solid var(--active-color); */
  border-top: none;
  visibility: hidden;
  animation-name: border-light;
  animation-duration: 4s;
  animation-iteration-count: infinite;  
  transition: .2s ease;
}
.submenu {
  
}
.sub--menu--item {
  margin-right: 10px;
  font-size: var(--regular-font-size);
  color: var(--second-font-color);
  /* border-bottom: 1px solid var(--active-color); */
  text-align: center;
}
.submenu:hover .sub--menu {
  /* transition: .3s ease; */
  visibility: visible;
}

.list--item a:hover {
  color: var(--active-color);
  font-weight: var(--heavy-font-weight);
  /* border-bottom: 1px solid var(--active-color); */
}
.sub--menu--item:hover {
  color: var(--active-color);
}

.drop__down__list {
  visibility: hidden;
  display: grid;
  grid-template-columns: 1fr 1fr;
  width: 100%;
  justify-content: flex-end;
  /* background-color: var(--border-color); */
  border: 1px solid var(--active-color);
  margin-bottom: 30px;
  ;
  padding: 15px;
  font-family: var(--second-font);
  border-radius: 10px;
  /* padding-left: 15px; */
}

.navbar {
  display: none;
  width: 50%;
  position: relative;
}

.navbar__container {
  display: flex;
  justify-content: space-around;
  align-items: center;
  height: 50px;
  /* position: relative; */
}

.navbar__container a {
  width: auto;
  height: 100%;
  text-decoration: none;
  font-size: var(--large-font-size);
  font-weight: var(--heavy-font-weight);
  color: var(--second-font-color);
  transition: .2s ease;
  position: relative;
}

.navbar__container a:hover {
  transform: scale(1.1);
}
.navbar--item {
  /* align-self: center; */
  display: flex;
  align-self: center;
  position: relative;
  height: 20px;
  font-family: var(--second-font);
  font-size: var(--large-font-size);
  color: var(--second-font-color);
  transition: .3s ease;
  cursor: pointer;
}

.navbar .drop__down__list {
  display: none;
  min-width: 300px;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  position: absolute;
  /* top: 40px; */
  top: 40px;
  left: 0;
  background-color: var(--dark-gray-color);
  margin: 0;
  padding: 15px;
  color: var(--second-font-color);
  border: none;
  /* left: 0; */
}


.navbar--item:hover {
  transform: scale(1.5);
  /* font-size: var(--jumbo-font-size); */
  color: var(--active-color);
  text-shadow: var(--box-shadow-color);
}


/*ANIMATIONS*/
@keyframes border-light {
  0% {
    /* border: 1px solid var(--second-font-color); */
  }
  50% {
    /* border: 1px solid var(--active-color); */
    box-shadow: var(--box-shadow-color);
  }
  100% {
    /* border: 1px solid var(--second-font-color); */
  }
}
/*HEADER QUERIES*/

@media only screen and ( min-width: 768px ) {
  .header {
    font-size: var(--jumbo-font-size);
  }

}

@media only screen and ( min-width: 1024px ) {
  .header {
  align-items: center;
  height: 50px;
  }
  .navbar {
    display: inline;
  }
  
  .icon--menu {
    display: none;
  }

  .drop__menu {
    display: none;
  }
  .header .close--icon {
    display: none;
  }
}